import Vue from 'vue'
import Router from 'vue-router'
import AdminPanel from '@/components/AdminPanel'
import StartPage from '@/components/StartPage'
import register from '@/components/Register'
import login from '@/components/Login'
import bookInfo from '@/components/bookInfo'
import Bag from '@/components/Bag'
import Profile from '@/components/Profile'
import Checkout from '@/components/checkout'
import Orders from '@/components/Orders'
import ProfileInfo from '@/components/profileInfo'
import addNewAuthor from '@/components/addAdminComponents/addNewAuthor'
import addNewGenre from '@/components/addAdminComponents/addNewGenre'
import addNewBook from '@/components/addAdminComponents/addNewBook'
import addNewUser from '@/components/addAdminComponents/addNewUser'
import changeAuthor from '@/components/changeAdminComponents/changeAuthor'
import changeGenre from '@/components/changeAdminComponents/changeGenre'
import changeBook from '@/components/changeAdminComponents/changeBook'
import changeUser from '@/components/changeAdminComponents/changeUser'
import showOrders from '@/components/showOrders'
// import changeAuthor from '@/components/changeAdminComponents/changeAuthor'

import VeeValidate from 'vee-validate';

Vue.use(VeeValidate);
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/adminPanel',
      name: 'adminPanel',
      component: AdminPanel,
      children: [
        {
          path: 'addNewAuthor',
          components:{addNewAuthor: addNewAuthor},
        },
        {
          path: 'addNewGenre',
          components:{addNewGenre: addNewGenre}
        },
        {
          path: 'addNewBook',
          components: {addNewBook: addNewBook}
        },
        {
          path: 'addNewUser',
          components: {addNewUser: addNewUser}
        },
        {
          path: 'showOrders',
          components: {showOrders: showOrders}
        },
        {
          path: 'changeAuthor/:id',
          components: {changeAuthor: changeAuthor}
        },
        {
          path: 'changeGenre/:id',
          components: {changeGenre: changeGenre}
        },
        {
          path: 'changeBook/:id',
          components: {changeBook: changeBook}
        },
        {
          path: 'changeUser/:id',
          components: {changeUser: changeUser}
        }
      ]
    },
    {
      path: '/',
      name: 'startPage',
      component: StartPage
    },
    {
      path: '/register',
      name: 'register',
      component: register
    },
    {
      path: '/login',
      name: 'login',
      component: login
    },
    {
      path: '/bookInfo/:id',
      name: 'bookInfo',
      component: bookInfo
    },
    // {
    //   path: '/profile/bag',
    //   name: 'bag',
    //   component: Bag,
    // },
    {
      path: '/profile',
      name: 'profile',
      component: Profile,
      children: [
        {
          path: 'bag',
          component: Bag
        },
        {
          path: 'orders',
          component: Orders
        },
        {
          //      /user/:id/profile
          //  <router-view>  User   UserProfile
          path: 'profileInfo',
          component: ProfileInfo
        }
      ]
    },
    {
      path: '/checkout/:price',
      name: 'Checkout',
      component: Checkout
    }
  ]
})



