<template>
     <div class="col-md-6 col-md-offset-3">
        <div class="table">
            <div>
                <div class="cell nuumber">N</div>
                <div class="cell date">Date of create</div>
                <div class="cell sum">Sum</div>
                <div class="cell status">Status</div>
            </div>
            <order-component 
            v-for="component in orders" 
            :orderComponents="component"></order-component>
        </div>
  </div>
    
</template>

<script>

import OrderComponent from '@/components/orderComponent'

export default {
    name: 'HelloWorld',
    data() {
        return {
            orders: ''
        }
    },
    methods: {
    
    },
    created() {
        var self = this;
        var id = this.$parent.$parent.getCookie('id');
        var hash = this.$parent.$parent.getCookie('hash');

        fetch(orderUrl+hash+'/'+id,{  
                method: 'get'})
            .then(this.$parent.$parent.status)
            .then(this.$parent.$parent.json)
            .then(function(data){
                if(data != false && data != null)
                {
                    self.orders = data;
                }      
        });
    },
    components: {
        OrderComponent
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

.table{
    text-align: center;
}


.cell{
    display: table-cell;
    font-weight: bold;
}

.nuumber{
    width: 33px;
}

.date{
    width: 361px;
}

.sum {
    width: 86px;
}

.status{
    width: 187px;
}
</style>



