<template>
    <div class="col-md-6">
        <form @submit.prevent="validateBeforeSubmit" >
            <div class="form-group" :class="{'has-error': errors.has('name') }">
                <label class="control-label" for="name">Author name</label>
                <input v-model="name"
                v-validate.initial="name"
                data-vv-rules="required|alpha"
                class="form-control" type="text" placeholder="name">
                <p class="text-danger" v-if="errors.has('name')">{{ errors.first('name') }}</p>
            </div>

            <div class="form-group" :class="{'has-error': errors.has('surname') }" >
                <label class="control-label" for="surname">Author surname</label>
                <input v-model="surname"
                v-validate.initial="surname" 
                data-vv-rules="required|alpha" 
                class="form-control" type="surname" placeholder="Surname">
                <p class="text-danger" v-if="errors.has('surname')">{{ errors.first('surname') }}</p>
            </div>

            <button class="btn btn" type="submit">Add Author</button>
        </form>
  </div> 
</template>


<script>
export default {
  data () {
    return {
      name: '',
      surname: ''
    }
},
methods: {
    validateBeforeSubmit(e) {
        var self = this;
        this.$validator.validateAll();

        if (!this.errors.any()) {
          this.$emit('addAuthor',this.name,this.surname);
        }
    },
}
}
</script>

