<template>
    <!-- <div>
        <label>Genre</label>
        <input class="form-control" placeholder="Genre" v-model="genreName">
        <button class="btn" v-on:click="changeGenre">Change Genre</button>
        <button class="btn" v-on:click="deleteGenre">Delete Genre</button>
  </div> -->

    <div class="col-md-6">
        <form @submit.prevent="validateBeforeSubmit" >
            <div class="form-group" :class="{'has-error': errors.has('name') }">
                <label class="control-label" for="name">Genre</label>
                <input v-model="name"
                v-validate.initial="name"
                data-vv-rules="required|alpha"
                class="form-control" type="text" placeholder="Genre">
                <p class="text-danger" v-if="errors.has('name')">{{ errors.first('name') }}</p>
            </div>
            <button class="btn btn" type="submit">Change Genre</button>
        </form>
        <button class="btn delete" v-on:click="deleteGenre">Delete Genre</button>
  </div>
</template>

<script>
export default {
  data () {
    return {
        id:'',
      name: ''
    }
},
methods: {
    validateBeforeSubmit(e) {
        var self = this;
        this.$validator.validateAll();

        if (!this.errors.any()) {
          this.$emit('changeGenre',this.id,this.name);
        }
    },
    deleteGenre: function () {
      this.$emit('deleteGenre',this.id);
    }
},
created () {
        var self = this;
    fetch(genreUrl+this.$route.params.id,{  
        method: 'get'})
    .then(this.$parent.$parent.status)
    .then(this.$parent.$parent.json)
    .then(function(data){
        if(data != false && data != null){
            self.name = data[self.$route.params.id].name;            
        }
    });
    this.id = this.$route.params.id;
}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .delete{
    margin-top: 25px;
  }
</style>



