<template>
<div>
    <form @submit.prevent="validateBeforeSubmit" >
        <div class="form-group" :class="{'has-error': errors.has('login') }">
            <label class="control-label" for="login">Login</label>
            <input v-model="login"
            v-validate.initial="login"
            data-vv-rules="required|alpha|min:4"
            class="form-control" type="text" placeholder="Login">
            <p class="text-danger" v-if="errors.has('login')">{{ errors.first('login') }}</p>
        </div>

        <div class="form-group" :class="{'has-error': errors.has('password') }" >
            <label class="control-label" for="password">Password</label>
            <input v-model="password"
            v-validate.initial="password" 
            data-vv-rules="required|min:4" 
            class="form-control" type="password" placeholder="Password">
            <p class="text-danger" v-if="errors.has('password')">{{ errors.first('password') }}</p>
        </div>

        <div class="form-group" :class="{'has-error': errors.has('name') }" >
            <label class="control-label" for="name">Name</label>
            <input v-model="name"
            v-validate.initial="name" 
            data-vv-rules="required|alpha" 
            class="form-control" type="name" placeholder="Name">
            <p class="text-danger" v-if="errors.has('name')">{{ errors.first('name') }}</p>
        </div>

        <div class="form-group" :class="{'has-error': errors.has('surname') }" >
            <label class="control-label" for="surname">Surname</label>
            <input v-model="surname" 
            v-validate.initial="surname" 
            data-vv-rules="required|alpha" 
            class="form-control" type="surname" placeholder="Surname">
            <p class="text-danger" v-if="errors.has('surname')">{{ errors.first('surname') }}</p>
        </div>

        <div class="form-group" :class="{'has-error': errors.has('phone') }" >
            <label class="control-label" for="phone">Mobile Phone</label>
            <input v-model="phone" 
            v-validate.initial="phone" 
            data-vv-rules="required|numeric|min:10" 
            class="form-control" type="phone" placeholder="Mobile Phone">
            <p class="text-danger" v-if="errors.has('phone')">{{ errors.first('phone') }}</p>
        </div>
                
        <div class="form-group" :class="{'has-error': errors.has('email') }" >
            <label class="control-label" for="email">Email</label>
            <input v-model="email"
                v-validate.initial="email" 
                data-vv-rules="required|email" 
                class="form-control" type="email" placeholder="Email">
            <p class="text-danger" v-if="errors.has('email')">{{ errors.first('email') }}</p>
        </div>

        <div class="form-group" :class="{'has-error': errors.has('discount') }" >
            <label class="control-label" for="discount">Discount</label>
            <input v-model="discount" 
            v-validate.initial="discount" 
            data-vv-rules="required|numeric" 
            class="form-control" type="discount" placeholder="Discount">
            <p class="text-danger" v-if="errors.has('discount')">{{ errors.first('discount') }}</p>
        </div>

        <div class="row">
            <div class="col-md-6 form-group":class="{'has-error': errors.has('isActive') }" >
            <label class="control-label" for="isActive">User Activity</label>
            <select v-model="isActive"
            v-validate.initial="isActive"
            data-vv-rules="required" 
            class="form-control select" >
                <option value="1">Active</option>
                <option value="0">No Active</option>
            </select>
            <p class="text-danger" v-if="errors.has('isActive')">{{ errors.first('isActive') }}</p>
            </div>

            <div class="col-md-6 form-group":class="{'has-error': errors.has('role') }" >
            <label class="control-label" for="role">User Role</label>
            <select v-model="role"
            v-validate.initial="role"
            data-vv-rules="required"
            class="form-control select" >
                <option value="user">User</option>
                <option value="admin">Admin</option>
            </select>
            <p class="text-danger" v-if="errors.has('role')">{{ errors.first('role') }}</p>
            </div>
        </div>
        <button class="btn" type="submit">Create User</button>
    </form>
</div>    
</template>

<script>
export default {
  data () {
    return {
        login: '',
        password: '',
        name: '',
        surname: '',
        phone: '',
        email: '',
        isActive: '',
        role: '',
        discount: '',
    }
},
methods: {
    validateBeforeSubmit(e) {
        this.$validator.validateAll();
        if (!this.errors.any()) {
          this.$emit('createUser',this.login,this.password,this.name,this.surname,
                  this.phone,this.email,this.isActive,this.role,this.discount);
        }
    },
    addUser: function () {
      //   


        console.log(this.login);
        console.log(this.password);
        console.log(this.discount);
        console.log(this.isActive);
        console.log(this.role);
        console.log(this.name);
        console.log(this.surname);
        console.log(this.phone);
        console.log(this.email);
        this.$emit('createUser');
        // console.log(this.surname);
    }
}
}
    //   <!-- <show-add-book v-if="showAddBook== true" @addBook="addBook" :genres="options.genres"
    //   :authors="options.authors"></show-add-book> -->
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

  input, label {
    display: block;
  }
</style>



