<template>
<div>
                <div class="tableRow"
                v-on:click="showAdditionalInfo(orderComponents.id,orderComponents.statusId)">
                    <div class="cell nuumber">
                        {{orderComponents.id}}
                    </div>
                    <div class="cell date">
                        {{orderComponents.createDate}}
                    </div>
                    <div class="cell sum">
                        {{orderComponents.totalPrice}}$
                    </div>
                    <div class="cell status">
                        {{orderComponents.name}}
                    </div>
                    </div>
                <div class="orderInfo col-md-offset-2"
                 v-bind:style="{ display: display}"
                 v-for="aditionalPart in aditionalInfo">
                    <div >
                        <label >Name</label>
                        {{aditionalPart.name}}
                    </div>
                    <div >
                        <label >Price:</label>
                        {{aditionalPart.bookPrice}}$
                    </div>
                    <div >
                        <label >Discount:</label>
                        {{aditionalPart.bookDiscount}}%
                    </div>
                    <div >
                        <label >Count:</label>
                        {{aditionalPart.count}}
                    </div>
                    
                </div>
                </div>
                <!-- </div> -->
            <!-- </table> -->

<!-- </div> -->
</template>

<script>
export default {
  data () {
    return {
        display: 'none',
        aditionalInfo: ''
    }
},
methods: {
    showAdditionalInfo: function (orderId,status) {
        var id = this.$parent.$parent.$parent.getCookie('id');
        var hash = this.$parent.$parent.$parent.getCookie('hash');
        var self = this;
        fetch(orderUrl+hash+'/'+id+'/'+status+'/'+orderId,{  
            method: 'get'})
        .then(this.$parent.$parent.$parent.status)
        .then(this.$parent.$parent.$parent.json)
        .then(function(data){
            if(data != false && data != null)
            {
                self.aditionalInfo = data;
            }      
        });
        if(this.display == 'none'){
            this.display = 'block';
        }else{
            this.display = 'none';
        }
    }
},
props: ['orderComponents'],
}

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

.tableRow{
    cursor: pointer;
}
.tableRow:hover{
    background-color: #FFE4C4;
}
.orderInfo:hover{
    background-color: #DEB887;
}
.orderInfo{
    text-align: left;
}
.cell{
    display: table-cell;
}
.additionalInfo{
    display:none;
}

.nuumber{
    width: 33px;
}

.date{
    width: 361px;
}

.sum {
    width: 86px;
}

.status{
    width: 187px;
}
</style>



