<template>
    <div class="col-md-6">
        <form @submit.prevent="validateBeforeSubmit" >
            <div class="form-group" :class="{'has-error': errors.has('name') }">
                <label class="control-label" for="name">Author name</label>
                <input v-model="name"
                v-validate.initial="name"
                data-vv-rules="required|alpha"
                class="form-control" type="text" placeholder="name">
                <p class="text-danger" v-if="errors.has('name')">{{ errors.first('name') }}</p>
            </div>

            <div class="form-group" :class="{'has-error': errors.has('Surname') }" >
                <label class="control-label" for="surname">Author surname</label>
                <input v-model="surname"
                v-validate.initial="surname" 
                data-vv-rules="required|alpha" 
                class="form-control" type="surname" placeholder="Surname">
                <p class="text-danger" v-if="errors.has('surname')">{{ errors.first('surname') }}</p>
            </div>

            <button class="btn btn" type="submit">Change Author</button>
        </form>
            <button class="btn delete" v-on:click="deleteAuthor">Delete Author</button>
  </div> 
</template>

<script>
export default {
  data () {
    return {
        id:'',
        name: '',
        surname: ''
    }
},
methods: {
    validateBeforeSubmit(e) {
        var self = this;
        this.$validator.validateAll();

        if (!this.errors.any()) {
          this.$emit('changeAuthor',this.id,this.name,this.surname);
        }
    },
    deleteAuthor: function () {
      this.$emit('deleteAuthor',this.id);
    }
},
created () {
    var self = this;
    fetch(authorUrl+this.$route.params.id,{  
        method: 'get'})
    .then(this.$parent.$parent.status)
    .then(this.$parent.$parent.json)
    .then(function(data){
        if(data != false && data != null){
            self.name = data[self.$route.params.id].name;       
            self.surname = data[self.$route.params.id].surname;       
        }
    });
    this.id = this.$route.params.id;
}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .delete{
    margin-top: 25px;
  }

</style>



