<template>
<div>
    <div>
        <div class="col-md-8 content">
            <div class="book">
                <div>
                    <img src="" class="bookImg"/>
                    <div class="bookInfo">
                        <h3>{{name}}</h3>
                        <div class="">
                            <label>Description</label>
                            <p>{{description}}</p>
                        </div>
                        <div class="row">
                            <div class="col-md-6">
                                <label>Book authors</label>
                                <ul>
                                    <li v-for="author in authors">
                                        {{author.name}} {{author.surname}}
                                    </li>
                                </ul>
                            </div>
                            <div class="col-md-6 title">
                                <label>Book genres</label>
                                <ul>
                                    <li v-for="genre in genres">
                                        {{genre.name}}
                                    </li>
                                </ul>
                            </div>    
                        </div>
                        <div class="row">
                            <div class="col-md-6">
                                <label>Book price</label>
                                <p>{{price}}$</p>
                            </div>
                            <div class="col-md-6">

                                <label>Book discount</label>
                                <p>{{discount}}%</p>
                            </div>
                        </div>
                        <div v-if="role=='user'">

                            <button class="btn btn-info" 
                            v-if="loginStats" v-on:click="addToBag">Add to Bag</button> 
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</div>    
</template>

<script>

export default {
    name: 'bookInfo',
    data() {
        return {
            bookId: '',
            userId: '',
            name: '',
            price: '',
            description: '',
            discount: '',
            authors: '',
            genres: '',
            role: '',
            loginStats: false,
            activeNoActiveAuthors: {
                active: false,
                noActive: true
                },
            activeNoActiveGenres: {
                active: false,
                noActive: true
            },
             activeNoActiveBooks: {
                active: false,
                noActive: true
            },
            options: {
                authors: '',
                genres: '',
                books: ''
            }
        }
    },
    methods: {
        addToBag: function () {
            var userId = this.$parent.getCookie('id');
            var hash = this.$parent.getCookie('hash');
            var self = this;
            fetch(bagUrl, {
                method: 'post',
                headers: {  
                    "Content-type": "application/x-www-form-urlencoded; charset=UTF-8"  
                },  
                body: 'bookId='+JSON.stringify(this.bookId)+'&clientId='+JSON.stringify(userId)
                +'&count='+JSON.stringify(1)+'&hash='+JSON.stringify(hash)
            })
            .then(this.$parent.status)
            .then(this.$parent.json)
            .then(function (data) {
                if(data != false){
                    alert('Book successful added to bag');
                }
            });
        }
    },
    created() {
        var self = this;
        this.loginStats = this.$parent.loginStatus;
        this.role = this.$parent.role;
        fetch(bookUrl+this.$route.params.id,{  
                method: 'get'})
            .then(this.$parent.status)
            .then(this.$parent.json)
            .then(function(data){
                if(data != false)
                {
                    self.bookId = data[self.$route.params.id].id;
                    self.name = data[self.$route.params.id].name;
                    self.price = data[self.$route.params.id].price;
                    self.description = data[self.$route.params.id].description;
                    self.discount = data[self.$route.params.id].discount;
                    self.authors = data[self.$route.params.id].authors;
                    self.genres = data[self.$route.params.id].genres;
                }     
        });
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    .bookImg{
    float: left;
    width: 250px;
    height: 380px;
}
.bookInfo{
    margin-left: 25px;
    display: inline-block;
}

.bookInfo li{
   width:200px;
}
</style>



