<template>
  <div id="app">
    <button type="button" class="btn btn-success home"
        v-on:click="home" >Home</button>
    <div v-if="loginStatus == false" class="row">
        <button type="button" class="btn btn-success register"
        v-on:click="register" >
        Register</button>
        <button type="button" class="btn btn-success login"
        v-on:click="login"
        >Login</button>
    </div>
    <div v-else class="row">
        <button type="button" class="btn btn-success profile"
        v-if="role == 'admin'"
        v-on:click="adminPanel" >Admin Panel</button>
        <button v-if="role == 'user'"
        v-on:click="profile"
        type="button" class="btn btn-success profile" >Profile</button>
        <button v-if="role == 'user'"
        v-on:click="bag"
        type="button" class="btn btn-primary profile" >Bag</button>
        <button type="button" class="btn btn-success logout"
        v-on:click="logout" >Logout</button>
    </div>
    <!-- <img src="./assets/logo.png"> -->
    <router-view/>
  </div>
</template>

<script>
export default {
  name: 'app',
  data () {
    return {
      loginStatus: false,
      role: '',
    }
  },
  methods: {
    home: function () {
      this.$router.push({ path: '/' });
    },
    login: function () {
        this.$router.push({ path: '/login' });
    },
    register: function () {
        this.$router.push({ path: '/register' });
    },
    logout: function () {
        document.cookie ='id =; expires=Thu, 01 Jan 1970 00:00:01 GMT;';
        document.cookie ='hash =; expires=Thu, 01 Jan 1970 00:00:01 GMT;';
        document.cookie ='role =; expires=Thu, 01 Jan 1970 00:00:01 GMT;';
        this.loginStatus = false;
        this.$router.push({ path: '/' });
    },
    adminPanel: function () {
        this.$router.push({ path: '/adminPanel' });
    },
    profile: function () {
      this.$router.push({ path: '/profile' });
    },
    bag: function () {
      this.$router.push({ path: '/profile/bag' });
    },
    getCookie: function (name) {
      var matches = document.cookie.match(new RegExp(
          "(?:^|; )" + name.replace(/([\.$?*|{}\(\)\[\]\\\/\+^])/g, '\\$1') + "=([^;]*)"
      ));
      return matches ? decodeURIComponent(matches[1]) : undefined;
    },
    status: function (response) { 
      if (response.status >= 200 && response.status < 300) {  
          return Promise.resolve(response)  
      } else {  
          return Promise.reject(new Error(response.statusText))  
      }  
    },
    json: function(response,self) {
        return response.json()  
    },
  },
  created () {
        var id = this.getCookie('id');
        var hash = this.getCookie('hash');
        var role = this.getCookie('role');

        if(id == undefined && hash == undefined&& role == undefined)
        {
            this.loginStatus = false;
        } else{
            this.loginStatus = true;
            this.role = role;
        }
  }

}
</script>

<style>
.login,
.register,
.profile,
.logout {
    float: right;
    margin-right: 50px;
    margin-top: 20px;
    margin-bottom: 20px;
}

.home{
  float: left;
  margin-top: 20px;
  margin-left: 50px;
  margin-bottom: 20px;
}
