<template>
  <div class="col-md-6">
        <form @submit.prevent="validateBeforeSubmit" >
            <div class="form-group" :class="{'has-error': errors.has('name') }">
                <label class="control-label" for="name">New Genre</label>
                <input v-model="name"
                v-validate.initial="name"
                data-vv-rules="required|alpha"
                class="form-control" type="text" placeholder="Genre">
                <p class="text-danger" v-if="errors.has('name')">{{ errors.first('name') }}</p>
            </div>

            <button class="btn btn" type="submit">Add Genre</button>
        </form>
  </div>
</template>

<script>
export default {
  data () {
    return {
      name: ''
    }
},
methods: {
    validateBeforeSubmit(e) {
      var self = this;
      this.$validator.validateAll();

      if (!this.errors.any()) {
        this.$emit('addGenre',this.name);
      }
  },
}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

  input, label {
    display: block;
  }
</style>



