<template>
    <div>
        <div class="col-md-2 menu">       
            <div >
                <div>
                    <div class="authors" v-on:click="showUnshowAuthors">
                        Authors
                    </div>
                    <div v-for="authorInfo in options.authors" v-bind:class="activeNoActiveAuthors" >
                        <label class="radio-inline link-text">
                            <input  type="radio" :value="authorInfo.id" name="1234" v-model="author">
                        {{authorInfo.name}} {{authorInfo.surname}}</label>
                    </div>
                </div>
            </div>

                <div>
                    <div class="genres" v-on:click="showUnshowGenres">
                        Genres
                    </div>
                        <div v-for="genreInfo in options.genres" v-bind:class="activeNoActiveGenres" >
                            <label class="radio-inline link-text">
                                <input  type="radio" :value="genreInfo.id" name="145234" v-model="genre">
                                {{genreInfo.name}}</label>
                        </div>

                </div>

                <div>
                    <div class="books" v-on:click="showUnshowBooks">
                        Books
                    </div>
                        <ul>
                            <li class="link-text" v-bind:class="activeNoActiveBooks"
                            v-for="book in options.books"
                            v-on:click="showBook(book.id)">{{book.name}}</li>
                        </ul>
                </div>
            </div>

            <div class="col-md-7 content">
                <div class="book"v-for="a in filteredBook">
                    <div>
                        <img src="" class="bookImg"/>
                        <div class="bookInfo">
                            <label>Book naame</label>
                            <p>{{a.name}}</p>
                            <label>Book description</label>
                            <p>{{a.description}}</p>
                            <button class="btn btn-info"
                            v-on:click="showBook(a.id)">More info</button>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>

    </div>
</template>

<script>

export default {
    name: 'HelloWorld',
    data() {
        return {
            author: '',
            genre: '',
            loginStats: false,
            role: '',
            activeNoActiveAuthors: {
                active: false,
                noActive: true
                },
            activeNoActiveGenres: {
                active: false,
                noActive: true
            },
             activeNoActiveBooks: {
                active: false,
                noActive: true
            },
            options: {
                authors: '',
                genres: '',
                books: ''
            }
        }
    },
    methods: {
        showUnshowAuthors: function () {
            this.showUnshow(this.activeNoActiveAuthors);
        },
        showUnshowGenres: function () {
            this.showUnshow(this.activeNoActiveGenres);
        },
        showUnshowBooks: function () {
            this.showUnshow(this.activeNoActiveBooks);
        },
        showUnshow: function (object) {
            if(object.active == true){
                object.active = false;
            }else{
                object.active = true;
            }

            if(object.noActive == true){
                object.noActive = false;
            }else{
                object.noActive = true
            }
        },
        showBook: function (id){
            this.$router.push({ path: '/BookInfo/'+id });
            
        }
    },
    computed: {
        filteredBook (){
            let filterResult = {}
            console.log(this.author);
            console.log(this.genre);
            if(this.author !='')
            {
                for(var book in this.options.books)
                    {
                        if(this.options.books[book].authors[this.author]){
                            filterResult[book] = this.options.books[book];
                        }
                    }
                    this.author = '';
            } else if (this.genre !='') {
                for(var book in this.options.books)
                    {
                        if(this.options.books[book].genres[this.genre]){
                            filterResult[book] = this.options.books[book];
                        }
                    }
                    this.genre = '';
            }else{
                filterResult = this.options.books;
            }

            return filterResult;

        }
    },
    created() {
        var self = this;

        fetch(authorUrl,{  
                method: 'get'})
            .then(this.$parent.status)
            .then(this.$parent.json)
            .then(function(data){
            self.options.authors = data;       
        });

        fetch(genreUrl,{  
                method: 'get'})
            .then(this.$parent.status)
            .then(this.$parent.json)
            .then(function(data){
            self.options.genres = data;        
        });

        fetch(bookUrl,{  
                method: 'get'})
            .then(this.$parent.status)
            .then(this.$parent.json)
            .then(function(data){
            self.options.books = data;     
        });
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

.menu {
    padding-left: 40px;
}

.menu input{
    visibility: hidden;
}

.menu label:hover{
    background-color: #F0F8FF;
}

.active{
    display: block;
    /* padding-left: 20px; */
}
.noActive{
    display: none;
}

.link-text{
    color:#0000FF;
    text-decoration: underline;
    cursor: pointer;
}
.authors,.genres,.books {
    color: 	#6495ED;
    font-size: 20px;
    cursor: pointer;
}

.bookImg{
    /* float: left; */
    width: 190px;
    height: 280px;
}
.bookInfo{
    /* float: left; */
    margin-left: 25px;
    display: inline-block;
}
.book{
    border: 1px solid grey;
    margin-bottom: 10px;
}
.book > div{
    margin: 5px 5px;
}

</style>



