<template>
     <div class="col-md-6 col-md-offset-3">
        <form @submit.prevent="validateBeforeSubmit" >
            <div class="form-group" :class="{'has-error': errors.has('login') }">
                <label class="control-label" for="login">Login</label>
                <input v-model="login"
                v-validate.initial="login"
                data-vv-rules="required|alpha|min:4"
                class="form-control" type="text" placeholder="Login">
                <p class="text-danger" v-if="errors.has('login')">{{ errors.first('login') }}</p>
            </div>

            <div class="form-group" :class="{'has-error': errors.has('password') }" >
                <label class="control-label" for="password">Password</label>
                <input v-model="password"
                v-validate.initial="password" 
                data-vv-rules="required|min:4" 
                class="form-control" type="password" placeholder="Password">
                <p class="text-danger" v-if="errors.has('password')">{{ errors.first('password') }}</p>
            </div>

            <button class="btn btn-primary btn-block" type="submit">Login</button>
        </form>
    </div>
  </div>
</template>

<script>

export default {
    data() {
        return {
            login:'',
            password: '',
            // loginError: {
            //     error: false,
            //     noError: true
            //     },
            // passwordError: {
            //     error: false,
            //     noError: true
            // },
            // authUrl: 'http://bookshop/user14/bookShopRest/client/api/auth/'
        }
    },
    methods: {
        validateBeforeSubmit(e) {
            var self = this;
            this.$validator.validateAll();

            if (!this.errors.any()) {
                fetch(authUrl, {
                    method: 'put',
                    headers: {  
                        "Content-type": "application/x-www-form-urlencoded; charset=UTF-8"  
                    },  
                    body: 'login='+JSON.stringify(this.login)+'&password='+JSON.stringify(this.password)
                })
                .then(this.$parent.status)
                .then(this.$parent.json)
                .then(function (data) {
                    if(data == 'Sorry your account is Not Active'){
                        alert('Sorry your account is Not Active');
                    }else if(data == 'wrong password')
                    {
                        alert('wrong login or password');
                    }else
                    {
                        document.cookie = "hash="+data.hash+'; path=/';
                        document.cookie = "id="+data.id+'; path=/';
                        document.cookie = "role="+data.role+'; path=/';
                        self.$parent.loginStatus = true;
                        self.$parent.role = data.role;
                        self.$router.push({ path: '/' });
                        // self.$emit('login','123');
                    }
                });
            }
        },
    }
}
</script>

