<template>
<div>
                <div class="tableRow">
                    <div class="cell nuumber"
                    v-on:click="showAdditionalInfo(orderComponents.id,orderComponents.statusId)">
                        {{orderComponents.id}}
                    </div>
                    <div class="cell date"
                    v-on:click="showAdditionalInfo(orderComponents.id,orderComponents.statusId)">
                        {{orderComponents.createDate}}
                    </div>
                    <div class="cell sum"
                    v-on:click="showAdditionalInfo(orderComponents.id,orderComponents.statusId)">
                        {{orderComponents.totalPrice}}
                    </div>
                    <div class="cell status">
                        <select v-on:change="changeStatus($event,orderComponents.id)"
                         class="form-control select">
                            <option :value="orderComponents.statusId">{{orderComponents.name}}</option>
                            <option v-for="statusInfo in allStatuses" v-if="orderComponents.statusId != statusInfo.id"
                            :value="statusInfo.id">{{statusInfo.name}}</option>
                        </select>
                    </div>
                    </div>
                <div class="orderInfo col-md-offset-2"
                 v-bind:style="{ display: display}"
                 v-for="aditionalPart in aditionalInfo">
                    <div >
                        <label >Name</label>
                        {{aditionalPart.name}}
                    </div>
                    <div >
                        <label >Price:</label>
                        {{aditionalPart.bookPrice}}$
                    </div>
                    <div >
                        <label >Discount:</label>
                        {{aditionalPart.bookDiscount}}%
                    </div>
                    <div >
                        <label >Count:</label>
                        {{aditionalPart.count}}
                    </div>
                    
                </div>
                </div>
</template>

<script>
export default {
  data () {
    return {
        display: 'none',
        aditionalInfo: '',
        allStatuses: ''
    }
},
methods: {
    showAdditionalInfo: function (orderId,status) {
        var id =''
        if(this.userId != undefined)
        {
            id = this.userId;
        }else{
            id = this.$parent.$parent.$parent.getCookie('id');
        }
        var self = this;
        var hash = this.$parent.$parent.$parent.getCookie('hash');
        fetch(orderUrl+hash+'/'+status+'/'+orderId,{  
            method: 'get'})
        .then(this.$parent.$parent.$parent.status)
        .then(this.$parent.$parent.$parent.json)
        .then(function(data){
            if(data != false && data != null)
            {
                self.aditionalInfo = data;
            }      
        });

        if(this.display == 'none'){
            this.display = 'block';
        }else{
            this.display = 'none';
        }
    },
    changeStatus: function (event,orderId){
        var hash = this.$parent.$parent.$parent.getCookie('hash');
        fetch(orderUrl, {
            method: 'put',
            headers: {  
                "Content-type": "application/x-www-form-urlencoded; charset=UTF-8"  
            },  
            body: 'orderId='+JSON.stringify(orderId)+'&statusId='+JSON.stringify(event.target.value)
            +'&hash='+JSON.stringify(hash)
        })
        .then(this.$parent.status)
        .then(this.$parent.json)
        .then(function (data) {
        });
    }
},
props: ['orderComponents','userId'],
created () {
    var id = ''
        var self = this;
        fetch(statusUrl,{  
            method: 'get'})
        .then(this.$parent.$parent.$parent.status)
        .then(this.$parent.$parent.$parent.json)
        .then(function(data){
            // console.log(data);
            if(data != false && data != null)
            {
                self.allStatuses = data;
            }      
        });

},
}

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

.tableRow{
    cursor: pointer;
}
.tableRow:hover{
    background-color: #FFE4C4;
}
.orderInfo:hover{
    background-color: #DEB887;
}
.orderInfo{
    text-align: left;
}

.cell{
    display: table-cell;
}
.additionalInfo{
    display:none;
}

.nuumber{
    width: 33px;
}

.date{
    width: 361px;
}

.sum {
    width: 86px;
}

.status{
    width: 187px;
}
</style>



