<template>
  <div class="hello">

    <div class="col-md-3 line">
      <div class="adminMenu">
        <div class="authorSection">
          <button class="btn" v-on:click="addAuthorField">Add author</button>
          <label>Edit or Delete Author</label>
          <select class="form-control select" size='3' v-model="author" v-on:change="showUnshow" 
            v-on:click="changeAuthorField">
            <option v-for="author in options.authors" 
            :value="author.id" >{{author.name}} {{author.surname}}</option>
          </select>
        </div>

        <div class="genreSection">
          <button class="btn" v-on:click="addGenreField">Add genre</button>
          <label>Edit or Delete Genre</label>
          <select class="form-control select" size='3' v-model="genre" v-on:change="showUnshow" 
          v-on:click="changeGenreField">
            <option v-for="genre in options.genres" 
            :value="genre.id">{{genre.name}}</option>
          </select>
        </div>

        <div class="bookSection">
          <button class="btn" v-on:click="addBookField">Add book</button>
          <label>Edit book</label>
          <select class="form-control select" size='3' v-model="book" v-on:change="showUnshow"
          v-on:click="changeBookField">
            <option v-for="book in options.books" 
            :value="book.id">{{book.name}}</option>
          </select>
        </div>

        <div class="userSection">
          <button class="btn" v-on:click="addUserField">Add user</button>
          <label>Edit user</label>
          <select class="form-control select" size='3' v-model="user" v-on:change="showUnshow"
          v-on:click="changeUserField">
            <option v-for="user in options.users" 
            :value="user.id">{{user.name}} {{user.surname}}</option>
          </select>
        </div>

        <div class="orderSection">
          <button class="btn" v-on:click="showOrders">Show  all orders</button>
        </div>
      </div>  
    </div>
    <div class="col-md-9 workField">
      <router-view name="addNewAuthor"
      @addAuthor="addAuthor"></router-view>

      <router-view name="addNewGenre"
      @addGenre="addGenre"></router-view>

      <router-view name="addNewBook"
       @addBook="addBook"></router-view>

      <router-view name="addNewUser"
      @createUser="createUser"></router-view>

      <router-view name="showOrders"></router-view>

      <router-view
      @changeAuthor="changeAuthor" 
      @deleteAuthor="deleteAuthor"
      name="changeAuthor"></router-view>

      <router-view
      @changeGenre="changeGenre"
      @deleteGenre="deleteGenre"
      name="changeGenre"></router-view>

      <router-view
      @changeBook="changeBook"
      name="changeBook"></router-view>

      <router-view
      @changeUser="changeUser"
      name="changeUser"></router-view>

  </div>

</div> 
</template>

<script>

export default {
  name: 'HelloWorld',
  data () {
    return {
      author: '',
      genre: '',
      book: '',
      newBook: {
        'price': '',
        'name': '',
        'description': '',
        'discount': '',
        'genreId': [],
        'authorId': []
        },
      clickCount:1,
      user: '',
      options: {
        authors: '',
        genres: '',
        books: '',
        users: '',
      }
    }
  },
  methods: {
    addAuthorField: function () {
      this.$router.push({ path: '/adminPanel/addNewAuthor'} );
    },
    showUnshow: function (){
      // if(this.clickCount % 2){
         this.$router.push({ path: '/adminPanel'} );
      // }else{

      // }
      // this.clickCount++
    },
    addAuthor: function (name,surname) {
      var self = this;
      var hash = this.$parent.getCookie('hash');
      fetch(authorUrl, {
          method: 'post',
          headers: {  
              "Content-type": "application/x-www-form-urlencoded; charset=UTF-8"  
          },  
          body: 'name='+JSON.stringify(name)+'&surname='+JSON.stringify(surname)
          +'&hash='+JSON.stringify(hash)
      })
      .then(this.$parent.status)
      .then(this.$parent.json)
      .then(function (data) {
          if(data == true){
            fetch(authorUrl,{  
                method: 'get'})
            .then(self.$parent.status)
            .then(self.$parent.json)
            .then(function(data){
              if(data != false)
              {
                self.options.authors = data;       
                alert('Author successful added');
                self.$router.push({ path: '/adminPanel'} );
              }
            });
          }
      });

    },
    changeAuthorField: function () {
      this.$router.push({ path: '/adminPanel/changeAuthor/'+this.author} );
    },
    changeAuthor: function (id,name,surname) {
      this.$router.push({ path: '/adminPanel'} );
      var self = this;
      var hash = this.$parent.getCookie('hash');
      fetch(authorUrl, {
          method: 'put',
          headers: {  
              "Content-type": "application/x-www-form-urlencoded; charset=UTF-8"  
          },  
          body: 'id='+JSON.stringify(id)+'&name='+JSON.stringify(name)+'&surname='+JSON.stringify(surname)
          +'&hash='+JSON.stringify(hash)
      })
      .then(this.$parent.status)
      .then(this.$parent.json)
      .then(function (data) {
          if(data == true){
            alert('Author successful changed')
            var changedAuthor = {'id':id,'name':name,'surname':surname}
            self.options.authors[id] = changedAuthor;
            self.$router.push({ path: '/adminPanel'} );
            
          }
      });
    },
    deleteAuthor: function (id) {
      var self = this;
      var hash = this.$parent.getCookie('hash');
      fetch(authorUrl, {
          method: 'delete',
          headers: {  
              "Content-type": "application/x-www-form-urlencoded; charset=UTF-8"  
          },  
          body: 'id='+JSON.stringify(id)+'&hash='+JSON.stringify(hash)
      })
      .then(this.$parent.status)
      .then(this.$parent.json)
      .then(function (data) {
          if(data == true){
            alert('Author successful deleted');
            self.$delete(self.options.authors, id);
            self.$router.push({ path: '/adminPanel'} );
            
          }
      });
    },
    addGenreField: function () {
      this.$router.push({ path: '/adminPanel/addNewGenre' });
    },
    addGenre: function (name) {
      var self = this;
      var hash = this.$parent.getCookie('hash');
      fetch(genreUrl, {
          method: 'post',
          headers: {  
              "Content-type": "application/x-www-form-urlencoded; charset=UTF-8"  
          },  
          body: 'name='+JSON.stringify(name)+'&hash='+JSON.stringify(hash)
      })
      .then(this.$parent.status)
      .then(this.$parent.json)
      .then(function (data) {
          if(data == true){
            fetch(genreUrl,{  
                method: 'get'})
            .then(self.$parent.status)
            .then(self.$parent.json)
            .then(function(data){
              if(data != false)
              {
                self.options.genres = data;       
                self.$router.push({ path: '/adminPanel'} );
                alert('Genre successful added');
                
              }
            });
          }
      });
    },
    changeGenreField: function () {
      this.$router.push({ path: '/adminPanel/changeGenre/'+this.genre} );
    },
    changeGenre: function (id,name) {
      var self = this;
      var hash = this.$parent.getCookie('hash');
      fetch(genreUrl, {
          method: 'put',
          headers: {  
              "Content-type": "application/x-www-form-urlencoded; charset=UTF-8"  
          },  
          body: 'id='+JSON.stringify(id)+'&name='+JSON.stringify(name)+'&hash='+JSON.stringify(hash)
      })
      .then(this.$parent.status)
      .then(this.$parent.json)
      .then(function (data) {
          if(data == true){
            alert('Genre successful changed');
            var changedGenre = {'id':id,'name':name};
            self.options.genres[id] = changedGenre;
            self.$router.push({ path: '/adminPanel'} );
            
          }
      });

      
    },
    deleteGenre: function (id) {
      var self = this;
      var hash = this.$parent.getCookie('hash');
      fetch(genreUrl, {
          method: 'delete',
          headers: {  
              "Content-type": "application/x-www-form-urlencoded; charset=UTF-8"  
          },  
          body: 'id='+JSON.stringify(id)+'&hash='+JSON.stringify(hash)
      })
      .then(this.$parent.status)
      .then(this.$parent.json)
      .then(function (data) {
          if(data == true){
            alert('Genre successful deleted');
            self.$delete(self.options.genres, id);
            self.$router.push({ path: '/adminPanel'} );
            
          }
      });
    },
    addBookField: function () {
      this.$router.push({ path: '/adminPanel/addNewBook'});
    },
    addBook: function (name,description,price,discount,genres,authors) {
      var self = this;
      var hash = this.$parent.getCookie('hash');
      fetch(bookUrl, {
          method: 'post',
          headers: {  
              "Content-type": "application/x-www-form-urlencoded; charset=UTF-8"  
          },  
          body: 'name='+JSON.stringify(name)+'&description='+JSON.stringify(description)
          +'&price='+JSON.stringify(price)+'&discount='+JSON.stringify(discount)
          +'&genres='+JSON.stringify(genres)+'&authors='+JSON.stringify(authors)
          +'&hash='+JSON.stringify(hash)
      })
      .then(this.$parent.status)
      .then(this.$parent.json)
      .then(function (data) {
          if(data == true){
            fetch(bookUrl,{  
                method: 'get'})
            .then(self.$parent.status)
            .then(self.$parent.json)
            .then(function(data){
              if(data != false ){
                self.options.books = data;    
                self.$router.push({ path: '/adminPanel'} );
                alert('Book successful added');
                
              }
            });

          }
      });
    },
    changeBookField: function () {
      this.$router.push({ path: '/adminPanel/changeBook/'+this.book} );
    },
    changeBook: function (id,name,description,price,discount,
        newBookGenres,newBookAuthors,oldGenres,oldAuthors) {
      var self = this;
      var hash = this.$parent.getCookie('hash');
      fetch(bookUrl, {
          method: 'put',
          headers: {  
              "Content-type": "application/x-www-form-urlencoded; charset=UTF-8"  
          },  
          body: 'name='+JSON.stringify(name)+'&description='+JSON.stringify(description)
          +'&price='+JSON.stringify(price)+'&discount='+JSON.stringify(discount)
          +'&newGenres='+JSON.stringify(newBookGenres)+'&newAuthors='+JSON.stringify(newBookAuthors)
          +'&oldAuthors='+oldAuthors+'&oldGenres='+oldGenres+'&bookId='+JSON.stringify(id)
          +'&hash='+JSON.stringify(hash)
      })
      .then(this.$parent.status)
      .then(this.$parent.json)
      .then(function (data) {
          if(data == true){
            alert('Book successful Changed');
            var changedBook = {'id':id,'name':name,'description':description,'price':price,
            'discount':discount,'genres':newBookGenres,'authors':newBookAuthors};
            self.options.books[id] = changedBook;
            self.$router.push({ path: '/adminPanel'} );
            
          }
      });
    },
    addUserField: function () {
      this.$router.push({ path: '/adminPanel/addNewUser'} );
    },
    createUser: function (login,password,name,surname,
                  phone,email,isActive,role,discount) {
      var self = this;
      var hash = this.$parent.getCookie('hash');
      fetch(authUrl, {
          method: 'post',
          headers: {  
              "Content-type": "application/x-www-form-urlencoded; charset=UTF-8"  
          },  
          body: 'login='+JSON.stringify(login)+'&password='+JSON.stringify(password)
          +'&name='+JSON.stringify(name)+'&surname='+JSON.stringify(surname)
          +'&phone='+JSON.stringify(phone)+'&email='+JSON.stringify(email)
          +'&isActive='+JSON.stringify(isActive)+'&role='+JSON.stringify(role)
          +'&discount='+JSON.stringify(discount)
      })
      .then(this.$parent.status)
      .then(this.$parent.json)
      .then(function (data) {
          if(data == true){
            fetch(userUrl+hash,{  
              method: 'get'})
                    .then(self.$parent.status)
                    .then(self.$parent.json)
                    .then(function(data){
                      self.options.users = data;        
                      alert('User successfully added');
                      self.$router.push({ path: '/adminPanel'} );
                  });
          } else if(data == 'login already exists'){
              alert('login already exists');
          }else{
              alert('Added error');
          }
      });
    },
    changeUserField: function () {
      this.$router.push({ path: '/adminPanel/changeUser/'+this.user} );
    },
    changeUser: function (login,password,name,surname,
                  phone,email,isActive,role,discount,userId) {
      var self = this;
      var hash = this.$parent.getCookie('hash');
      fetch(userUrl, {
          method: 'put',
          headers: {  
              "Content-type": "application/x-www-form-urlencoded; charset=UTF-8"  
          },  
          body: 'login='+JSON.stringify(login)+'&password='+JSON.stringify(password)
          +'&name='+JSON.stringify(name)+'&surname='+JSON.stringify(surname)
          +'&phone='+JSON.stringify(phone)+'&email='+JSON.stringify(email)
          +'&isActive='+JSON.stringify(isActive)+'&role='+JSON.stringify(role)
          +'&discount='+JSON.stringify(discount)+'&id='+JSON.stringify(userId)
          +'&hash='+JSON.stringify(hash)
      })
      .then(this.$parent.status)
      .then(this.$parent.json)
      .then(function (data) {
          if(data == true){
            alert('User successful Changed');
            self.$router.push({ path: '/adminPanel'} );
            
          }
      });
    },
    showOrders: function () {
      this.$router.push({ path: '/adminPanel/showOrders'});
    }
  },
  created () {
    if(this.$parent.loginStatus == false)
    {
      this.$router.push({ path: '/'});
    }else{
      if(this.$parent.role != 'admin'){
        this.$router.push({ path: '/'});
      }
    }
    var self = this;
    var hash = this.$parent.getCookie('hash');
      fetch(authorUrl,{  
            method: 'get'})
        .then(this.$parent.status)
        .then(this.$parent.json)
        .then(function(data){
          self.options.authors = data;       
      });

      fetch(genreUrl,{  
            method: 'get'})
        .then(this.$parent.status)
        .then(this.$parent.json)
        .then(function(data){
          self.options.genres = data;        
      });

      fetch(bookUrl,{  
            method: 'get'})
        .then(this.$parent.status)
        .then(this.$parent.json)
        .then(function(data){
          self.options.books = data;
      });

      fetch(userUrl+hash,{  
            method: 'get'})
        .then(this.$parent.status)
        .then(this.$parent.json)
        .then(function(data){
          self.options.users = data;        
      });
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
   .line { 
    border-right: 2px solid #ccc; /*   */ 
   }

  .adminMenu button{
    display: block;
  }

  .authorSection, .genreSection, .bookSection, .userSection {
    margin-bottom: 20px;
  }

  .workField input,.workField label {
    display: block;
  }
</style>



