<template>
<div class="col-md-6 col-md-offset-3">
    <form @submit.prevent="validateBeforeSubmit" >
        <div class="form-group" :class="{'has-error': errors.has('login') }">
            <label class="control-label" for="login">Login</label>
            <input v-model="login"
            v-validate.initial="login"
            data-vv-rules="required|alpha|min:4"
            class="form-control" type="text" placeholder="Login">
            <p class="text-danger" v-if="errors.has('login')">{{ errors.first('login') }}</p>
        </div>

        <div class="form-group">
            <label class="control-label" for="password">New password</label>
            <input v-model="password" 
            class="form-control" type="password" placeholder="Password">
        </div>

        <div class="form-group" :class="{'has-error': errors.has('name') }" >
            <label class="control-label" for="name">Name</label>
            <input v-model="name"
            v-validate.initial="name" 
            data-vv-rules="required|alpha" 
            class="form-control" type="name" placeholder="Name">
            <p class="text-danger" v-if="errors.has('name')">{{ errors.first('name') }}</p>
        </div>

        <div class="form-group" :class="{'has-error': errors.has('surname') }" >
            <label class="control-label" for="surname">Surname</label>
            <input v-model="surname" 
            v-validate.initial="surname" 
            data-vv-rules="required|alpha" 
            class="form-control" type="surname" placeholder="Surname">
            <p class="text-danger" v-if="errors.has('surname')">{{ errors.first('surname') }}</p>
        </div>

        <div class="form-group" :class="{'has-error': errors.has('phone') }" >
            <label class="control-label" for="phone">Mobile Phone</label>
            <input v-model="phone" 
            v-validate.initial="phone" 
            data-vv-rules="required|numeric|min:10" 
            class="form-control" type="phone" placeholder="Mobile Phone">
            <p class="text-danger" v-if="errors.has('phone')">{{ errors.first('phone') }}</p>
        </div>
                
        <div class="form-group" :class="{'has-error': errors.has('email') }" >
            <label class="control-label" for="email">Email</label>
            <input v-model="email"
                v-validate.initial="email" 
                data-vv-rules="required|email" 
                class="form-control" type="email" placeholder="Email">
            <p class="text-danger" v-if="errors.has('email')">{{ errors.first('email') }}</p>
        </div>

        <button class="btn" type="submit">Change Profile info</button>
    </form>
</div> 

</template>

<script>
export default {
  data () {
    return {
        login: '',
        password: '',
        name: '',
        surname: '',
        phone: '',
        email: '',
        userId: ''
    }
},
methods: {
    validateBeforeSubmit(e) {
        this.$validator.validateAll();
        if (!this.errors.any()) {
            var hash = this.$parent.$parent.getCookie('hash');
            fetch(userUrl, {
                method: 'put',
                headers: {  
                    "Content-type": "application/x-www-form-urlencoded; charset=UTF-8"  
                },  
                body: 'login='+JSON.stringify(this.login)+'&password='+JSON.stringify(this.password)
                +'&name='+JSON.stringify(this.name)+'&surname='+JSON.stringify(this.surname)
                +'&phone='+JSON.stringify(this.phone)+'&email='+JSON.stringify(this.email)
                +'&id='+JSON.stringify(this.userId)+'&hash='+JSON.stringify(hash)
            })
            .then(this.$parent.$parent.status)
            .then(this.$parent.$parent.json)
            .then(function (data) {
                if(data == true){
                    alert('Profile successful Changed');
                }
            });
        }
    },
},
created () {
    var self = this;
    var userId = this.$parent.$parent.getCookie('id');
    var hash = this.$parent.$parent.getCookie('hash');
    this.userId = userId;
    fetch(userUrl+hash+'/'+userId,{  
        method: 'get'})
    .then(this.$parent.$parent.status)
    .then(this.$parent.$parent.json)
    .then(function(data){
        if(data != false){
            self.login = data[userId].login;
            self.name = data[userId].name;
            self.surname = data[userId].surname;
            self.phone = data[userId].phone;
            self.email = data[userId].email;
        }
    });
},
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
button {
    float: right;
}

</style>



