<template>
    <tr >
        <td>
            <input type="checkbox" :value="bagComponents.bagId"
            v-on:click="deleteBagComponents" v-model="asd">
        </td>
        <td>
            {{bagComponents.posNumber}}
        </td>
        <td>
            {{bagComponents.name}}
        </td>
        <td>
            <button v-on:click="plus(bagComponents)">
                +
            </button>
            {{bagComponents.count}}
            <button v-on:click="minus(bagComponents)">
                -
            </button>
        </td>
        <td>
            {{bagComponents.price}}$
        </td>
        <td>
            {{bagComponents.bookDiscount}}%
        </td>
        <td>
            {{sumCalculate}}$
        </td>
    </tr>
</template>

<script>
export default {
  data () {
    return {
        asd: [],
        count: '',
        price: '',
        discount: '',
        sum: '',
        id: ''
    }
},
methods: {
    deleteBagComponents: function (event) {
        if(event.srcElement.checked == true)
        {
            this.$emit('deleteBagComponents', event.srcElement.value);
        }else{
            this.$emit('noDelete', event.srcElement.value);
        }
    },
    posNumber: function() {
        var newPos = this.startPosNumber+1;
        return newPos;
    },
    plus: function () {
        this.count++;
        this.$emit('changeCount', this.count,this.id);
    },
    minus: function () {
        if(this.count == 1){
            return;
        }
        this.count--;
        this.$emit('changeCount', this.count,this.id);
    }
},
props: ['bagComponents'],
created () {
    this.count = this.bagComponents.count
    this.price = this.bagComponents.price
    this.discount = this.bagComponents.bookDiscount
    this.id = this.bagComponents.id
},
computed: {
    sumCalculate () {
        var sum = this.count * this.price;
        var discount = sum*(this.discount/100);
        sum = sum - discount;
        this.sum = sum;
        return sum;
    },
}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



