<template>
<div class="row">
    <div class="col-md-6 col-md-offset-3 text-center">
        <button class="btn btn-primary" type="submit"
        v-on:click="showBag">Bag</button>
        <button class="btn btn-primary " type="submit"
        v-on:click="showOrders">Orders</button>
        <button class="btn btn-primary" type="submit"
        v-on:click="showProfileInfo">Profile infro</button>
    </div>
    <router-view></router-view>
</div>
</template>

<script>

export default {
    data() {
        return {
        }
    },
    methods: {
        showBag: function () {
            this.$router.push({ path: '/profile/bag' });
        },
        showProfileInfo: function () {
            this.$router.push({ path: '/profile/profileInfo' });
        },
        showOrders: function () {
            this.$router.push({ path: '/profile/orders' });
        }

    },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



