<template>
     <div class="col-md-6 col-md-offset-3 ">
         <div class="row">

            <div class="col-md-3 text-center col-md-offset-5">
                <div class="paymentSystems">
                    <div v-for="paymentSystem in paymentSystems">
                        <label class="radio-inline">
                            <input type="radio" :value="paymentSystem.id"
                            :name="paymentSystem.name"
                            v-model="pickedSystem">
                            {{paymentSystem.name}}</label>
                    </div>
               
                </div>
            </div>
         </div>
         <div class="text-center">
                
            <button class="btn btn-primary" type="submit"
            v-on:click="back">Back</button>
            <button class="btn btn-primary" type="submit"
            v-on:click="chekcout">Checkout</button>
         </div>
      </div>
</template>

<script>

export default {
    data() {
        return {
            paymentSystems: '',
            pickedSystem: ''
        }
    },
    methods: {
        chekcout: function () {
            var self = this;
            var userId = this.$parent.getCookie('id');
            var hash = this.$parent.getCookie('hash');
            if (this.pickedSystem != '') {
                fetch(orderUrl, {
                    method: 'post',
                    headers: {  
                        "Content-type": "application/x-www-form-urlencoded; charset=UTF-8"  
                    },  
                    body: 'userId='+JSON.stringify(userId)+'&paymentId='+JSON.stringify(this.pickedSystem)
                    +'&totalPrice='+JSON.stringify(this.$route.params.price)+'&hash='+JSON.stringify(hash)
                })
                .then(this.$parent.status)
                .then(this.$parent.json)
                .then(function (data) {
                    if(data != false){
                        alert('Thank you for your purchase');
                        self.$router.push({ path: '/profile' });
                    }
                });
            } else {
                alert('select paymentSystem');
            }
        },
        back: function () {
            this.$router.push({ path: '/profile/bag' });
        }
    },
    created () {
        var self = this;
        fetch(paymentUrl,{  
            method: 'get'})
        .then(this.$parent.status)
        .then(this.$parent.json)
        .then(function(data){
            if(data != false)
            {
                self.paymentSystems = data;       
            }
      });
    }

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.paymentSystems{
    text-align: left;
}

</style>



